import React from 'react';
import './header.css';
import img from '../pictures/logo.png'

class Header extends React.Component {

    render() {


        return (

            <div className="headContainer">
                <img src={img}></img>
                <div></div>
               
                <div className="speach">
                    <h2>דבר המפקד</h2>
                    <p> "לורם איפסום דולור סיט אמט, קונסקטורר אדיפיסינג אלית קולורס מונפרד אדנדום סילקוף, מרגשי ומרגשח. עמחליף לפרומי בלוף קינץ תתיח לרעח. לת צשחמי צש בליא, מנסוטו צמלח לביקו ננבי, צמוקו בלוקריה שיצמה ברורק. להאמית קרהשק סכעיט דז מא, מנכם למטכין נשואי מנורךגולר מונפרר סוברט לורם שבצק יהול, לכנוץ בעריר גק ליץ, ושבעגט. ושבעגט לבם סולגק. בראיט ולחת צורק מונחף, בגורמי מגמש. תרבנך וסתעד לכנו סתשם השמה - לתכי מורגם בורק? לתיג ישבעס."

                    </p>
                </div>
                <div></div>
            </div>
        )
    }
}

export default Header;