
const initState = {
    nameRedux: 'tamara',
    emailRedux: 'bbbbb@vvvvvv',
    formRedux: ''
}

const rootReducer = (state = initState, action) => {
    if (action.type === 'CHANGE_NAME') {
        let newName = action.newName;
        return {
            ...state,
            nameRedux: newName
        }

    }
    if (action.type === 'CHANGE_EMAIL') {
        let newEmail = action.newEmail;
        // console.log(newEmail)
        return {
            ...state,
           emailRedux: newEmail
        }

    }
    if (action.type === 'CHANGE_FORM') {
        let newForm = action.newForm;
        // console.log('fffffffff', newForm)
        return {
            ...state,
            formRedux: newForm
        }

    }
    return state;
}

export default rootReducer