import React from 'react';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import './Card.css';
import form from '../form/form';
import img from '../pictures/jobSeekers/man.jpg'

class Card extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'בן ברינברג',
            seekObus: 'מחפש עבודה',
            list: ['מתכנת', 'מנהל', 'ביולוג'],
            photo: '',
            city: 'כפר סבא',
            area: 'השרון',
            platoon: 'ג',
            form: false
        }
    }
    createList = () => {
        let list = [];
        for (let i = 0; i < this.props.elm.list.length; i++) {
            list.push(<li>{this.props.elm.list[i]}</li>)
        }

        return list
    }

    sendMail = (e) => {
        e.preventDefault()
        this.setState({ form: true })
        // let nameSoldier = this.props.elm.name
        // console.log(nameSoldier)
        this.props.changeName(this.props.elm.name)
        this.props.changeEmail(this.props.elm.email)
        this.props.changeForm(true)
        
    }

    render() {
        console.log(this.props.formRedux)
        if (this.props.formRedux) {

            return <Redirect to='/form' />
        } else {

            return (


                <div className='cardContainer' key={this.props.elm.index}>
                    <div className='leftSideCard'>
                        <div className='name'>
                            {this.props.elm.name}
                        </div>
                        <div className='seek'>
                            {this.props.elm.seekObus}                     </div>
                        <div className='field'>
                            <ul className="list">
                                {this.createList()}
                            </ul>
                        </div>

                    </div>
                    <div className='rightSideCard'>
                        <div className='photo'>
                            <img src={img}></img>
                        </div>
                        <div className='cityArea'> {this.props.elm.city}, {this.props.elm.area}</div>
                        <div className='platoon'>פלוגה {this.props.elm.platoon}</div>

                    </div>
                    <button onClick={this.sendMail} className="send">שלח הודעה</button>
                </div>
            )
        }
    }
}

const mapStateToProps = (state) => {
    return {
        nameRedux: state.nameRedux,
        emailRedux: state.emailRedux,
        formRedux: state.formRedux
    }

}

const dispatchToProps = (dispatch) => {
    return {
        changeName: (newName) => { dispatch({type:'CHANGE_NAME',newName:newName})},
        changeEmail: (newEmail) => { dispatch({type:'CHANGE_EMAIL',newEmail:newEmail})},
        changeForm: (newForm) => { dispatch({type:'CHANGE_FORM',newForm:newForm})}
    }
}

export default connect(mapStateToProps, dispatchToProps)(Card);