import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import './form.css';

class Form extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            yourName: '',
            yourPhone: '',
            yourEmail: '',
            text: ''

        }
    }
    //start trying mailing...
    handleYourName = (e) => {
        this.setState({
            yourName: e.target.value
        })
    }
    handleYourPhone = (e) => {
        this.setState({
            yourPhone: e.target.value
        })
    }
    handleYourEmail = (e) => {
        this.setState({
            yourEmail: e.target.value
        })
    }
    handleText = (e) => {
        this.setState({
            text: e.target.value
        })
    }


    handleSubmit = (e) => {
        e.preventDefault();
        // console.log(this.props.emailRedux)
        // console.log(this.props.nameRedux)
        // console.log(this.props.formRedux)
        this.props.changeForm(false)

        // this.setState({
        //     yourName: e.target.name.value,
        //     yourPhone: e.target.phone.value,
        //     yourEmail: e.target.mail.value,
        //     text: e.target.text.value
        // })
      let data ={
          name:this.state.yourName,
          phone:this.state.yourPhone,
          mail:this.state.yourEmail,
          textBody:this.state.text,
          sendTo:this.props.emailRedux
      }
//////\
fetch('/sendData', {

          method: 'POST',
          body: JSON.stringify(data),
          headers: {
            'Content-Type': 'application/json'
          }
        }).then(res=>{
            this.resetForm()
            console.dir(res)
            
        }).catch(()=>{
            console.log('message not send')
        })
      //////////
    //   axios.post('/api/forma', data)
    //   .then(res=>{
    //       this.resetForm()
    //       console.dir(res)
          
    //   }).catch(()=>{
    //       console.log('message not send')
    //   })


        alert('ההודעה נשלחה')
    }

    resetForm=()=>{
        this.setState({
            yourName: '',
            yourPhone: '',
            yourEmail: '',
            text: ''
        })
    }

    render() {
        if (!this.props.formRedux) {

            return <Redirect to='/' />
        } else {
            return (

                <div>
                    <form className="form" onSubmit={this.handleSubmit}>
                        <h1>טופס יצירת קשר עם {this.props.nameRedux}</h1>
                        <p>רשום שמך:</p>
                        <input
                         value={this.state.yourName}
                            onChange={this.handleYourName}
                            name="yourName" type="text" placeholder="שם" />
                        <p>הכנס טלפון</p>
                        <input
                        value={this.state.yourPhone}
                        onChange={this.handleYourPhone} name="phone" type="text" placeholder="טלפון" />
                        <p>רשום מייל לצורך התקשרות:</p>
                        <input 
                        value={this.state.yourEmail}
                        onChange={this.handleYourEmail}name="mail" type="text" placeholder="אימייל" />
                        <p>הודעה:</p>
                        <textarea 
                        value={this.state.text}
                        onChange={this.handleText}name="text" cols='30' rows='5' placeholder='רשום הודעה'></textarea>
                        <br></br>
                        <button type="submit" value="שלח טופס">שלח טופס</button>
                    </form>

                </div>
            )
        }
    }
}
const getNameFromRedux = (state) => {
    return {
        nameRedux: state.nameRedux,
        emailRedux: state.emailRedux,
        formRedux: state.formRedux
    }

}
const dispatchToProps = (dispatch) => {
    return {
        changeForm: (newForm) => { dispatch({ type: 'CHANGE_FORM', newForm: newForm }) }
    }
}

export default connect(getNameFromRedux, dispatchToProps)(Form);